<template>
    <!-- <v-responsive class="mx-auto" max-width="344"> -->

            <v-form v-model="valid">
                <v-container>
                    <v-row>
                        <v-col cols="12" md="6">
                            <v-text-field v-model="firstname" :rules="FnameRules" :counter="50" label="First name*" required validate-on=""></v-text-field>
                        </v-col>

                        <v-col cols="12" md="6">
                            <v-text-field v-model="lastname" :rules="LnameRules" :counter="50" label="Last name*" required></v-text-field>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col cols="12" md="12">
                            <v-text-field v-model="email" :rules="emailRules" label="E-mail*" required></v-text-field>
                        </v-col>
                    </v-row>
                    <!-- <v-row>
                        <v-col cols="12" md="6">
                            <v-text-field v-model="firstname" :rules="nameRules" :counter="10" label="First name"
                                required></v-text-field>
                        </v-col>

                        <v-col cols="12" md="6">
                            <v-text-field v-model="lastname" :rules="nameRules" :counter="10" label="Last name"
                                required></v-text-field>
                        </v-col>
                    </v-row> -->
                    <v-row>
                        <v-col cols="12">
                            <v-radio-group v-model="allergies" inline label="Do you have any allergies?">
                                <v-radio label="Yes" value="Yes"></v-radio>
                                <v-radio label="No" value="No"></v-radio>
                            </v-radio-group>
                        </v-col>
                    </v-row>
                    <v-row>
                        <v-col cols="12" md="6">
                            <v-radio-group v-model="look" label="How happy you are with your current look?">
                            <v-radio label="Very Happy" value="Very Happy"></v-radio>
                            <v-radio label="Somewhat Happy" value="Somewhat Happy"></v-radio>
                            <v-radio label="Not happy at all" value="Not happy at all"></v-radio>
                            </v-radio-group>
                        </v-col>
                        <v-col cols="12" md="6">
                            <!-- <v-label></v-label> -->
                            <v-radio-group v-model="changecut" label="How open are you to change your cut?">
                            <v-radio label="Very Open" value="Very Open"></v-radio>
                            <v-radio label="Somewhat Open" value="Somewhat Open"></v-radio>
                            <v-radio label="Not Open at all" value="Not Open at all"></v-radio>
                            </v-radio-group>
                        </v-col>
                    </v-row>

                    <v-row>
                        <v-col cols="12" md="6">
                            <!-- <v-label></v-label> -->
                            <v-radio-group v-model="changecolor" label="How open are you to change your color?">
                            <v-radio label="Very Open" value="Very Open"></v-radio>
                            <v-radio label="Somewhat Open" value="Somewhat Open"></v-radio>
                            <v-radio label="Not Open at all" value="Not Open at all"></v-radio>
                            </v-radio-group>
                        </v-col>
                        <v-col cols="12" md="6">
                            <!-- <v-label></v-label> -->
                            <v-radio-group v-model="changestyle" label="How open are you to change your style?">
                            <v-radio label="Very Open" value="Very Open"></v-radio>
                            <v-radio label="Somewhat Open" value="Somewhat Open"></v-radio>
                            <v-radio label="Not Open at all" value="Not Open at all"></v-radio>
                            </v-radio-group>
                        </v-col>
                    </v-row>

                    <v-row>
                        
                        <v-col cols="12">
                            <v-label class="cl ml-2 mb-1">How much time are you willing to spend on your hairs each day?</v-label>
                            <v-radio-group v-model="timeonhair" label="">
                                <v-radio label="5 min" value="5"></v-radio>
                                <v-radio label="10 min" value="10"></v-radio>
                                <v-radio label="15 min" value="15"></v-radio>
                                <v-radio label="20 min" value="20"></v-radio>
                                <v-radio label="30 min" value="30"></v-radio>
                            </v-radio-group>
                        </v-col>
                    </v-row>

                    <v-row>
                        <v-col cols="12">
                            <v-radio-group v-model="washhair" label="How often do you wash your hairs?">
                                <v-radio label="Daily" value="Daily"></v-radio>
                                <v-radio label="2-3d/week" value="2-3d/week"></v-radio>
                                <v-radio label="4-5d/week" value="4-5d/week"></v-radio>
                            </v-radio-group>
                        </v-col>
                    </v-row>

                    <v-row>
                        <v-col cols="12">
                            <v-checkbox v-model="agree" label="Do you agree?"></v-checkbox>
                        </v-col>
                    </v-row>

                </v-container>
            </v-form>
            <nav>
                <v-toolbar flat height="60" color="black">
                    <v-row align="stretch">
                        <v-col cols="12" class="d-flex justify-center align-center">
                            <v-btn prepend-icon="mdi-eraser" variant="outlined" @click="cleare" style="margin-right:10px;">clear</v-btn>
                            <v-btn prepend-icon="mdi-check-outline" variant="outlined" @click="submit" style="margin-left:10px;">submit</v-btn>
                        </v-col>
                    </v-row>
                </v-toolbar>
            </nav>
            <template>
                <div class="text-center">
                    
                    <v-dialog
                    v-model="dialog"
                    :scrim="false"
                    persistent
                    width="auto"
                    >
                    <v-card
                        color="black"
                    >
                        <v-card-text>
                        Submiting...
                        <v-progress-linear
                            indeterminate
                            color="white"
                            class="mb-2"
                        ></v-progress-linear>
                        </v-card-text>
                    </v-card>
                    </v-dialog>
                </div>
                </template>
    <!-- </v-responsive> -->

</template>


<script>
  export default {
    data: () => ({
        dialog: false,
        valid: false,
        firstname: '',
        lastname: '',
        FnameRules: [
        value => {
          if (value) return true

          return 'First Name is requred.'
        },
        value => {
          if (value?.length <= 50) return true

          return 'Name must be less than 50 characters.'
        },
      ],
      LnameRules: [
        value => {
          if (value) return true

          return 'Last Name is requred.'
        },
        value => {
          if (value?.length <= 50) return true

          return 'Last Name must be less than 50 characters.'
        },
      ],
      email: '',
      emailRules: [
        value => {
          if (value) return true

          return 'E-mail is requred.'
        },
        value => {
          if (/.+@.+\..+/.test(value)) return true

          return 'E-mail must be valid.'
        },
      ],
      allergies :'No',
      look : '',
      changecut : '',
      changecolor : '',
      changestyle : '',
      timeonhair : '',
      washhair : '',
      agree : false
    }),
    methods:{
        cleare(){
            this.firstname = ''
            this.lastname = ''
            this.email = ''
            this.allergies = 'No',
            this.look = '',
            this.changecut = '',
            this.changecolor = '',
            this.changestyle = '',
            this.timeonhair = '',
            this.washhair = '',
            this.agree = false
        },
        submit(){
            if (this.firstname!= '') {
                this.dialog = true;
                setTimeout(() => (this.dialog = false), 4000)
            }
        }
    },
    
  }
</script>

<style>
.cl{
    /* text-justify: auto; */
    white-space: break-spaces;
}
</style>